* {
  margin: 0;
  padding: 0;
}

.body {
  background-color: #212529;
}
/* Detail */

.title {
  font-size: 1.7rem;
  color: turquoise;
  padding-left: 1.5rem;
  padding-bottom: none;
}

.title2 {
  font-size: 1.7rem;
  color: turquoise;
  opacity: 1.0 !important;
  padding-bottom: 4px;
  margin-right: 3px;
}

.subtitle {
  font-size: 1.2rem;
  color: #ddd;
}

.icon {
  margin: -3px;
}

.badge {
  position: absolute;
  top: -7px;
  right: -5px;
}

.col {
  max-width: 45% !important;
}

.box {
  /* border: 2px solid currentColor; */
  border: 1px solid #2a3d3d;
  border-radius: 5px;
  text-align: center;
  display: block;
  justify-content: center;
  background-color: #25282d;
}

div.box {
  display: block;
  justify-content: center;
}

/* Sidebar */

.sidebar {
  width: 100%;
  height: 0vh;
  display: flex;
  flex-wrap: wrap;
}

.side {
  background-color: #25282d;
  z-index: 1 !important;
  opacity: 0.97;
  height: 99.9vh;
  width: 100vw;
  font-size: calc(20px + 2.5vmin);
  /* color: rgb(45, 182, 130); */
  border: 1px solid turquoise;
  border-radius: 5px;
  overflow-x: hidden;
}

.visible {
  z-index: 10;
}

.hidden {
  display: none;
}

@media only screen and (min-width: 900px) {
  .side {
    width: 25vw;
  }
}

/* Buttons */

.button {
  height: 3.5rem;
  width: 6rem;
  z-index: 1;
  color: rgb(31, 174, 160) !important;
  outline: 1px solid rgb(31, 174, 160) !important;
}

.close {
  height: 2rem;
  width: 2rem;
  margin-right: 19px;
  margin-top: 10px;
  padding: 0px 10px 3px 10px;
  color: rgb(31, 174, 160) !important;
  outline: 2px solid rgb(31, 174, 160) !important;
}

.button:hover {
  background-color: rgb(31, 174, 160) !important;
  color: #fff !important;
}




.asia:focus,
.southamerica:focus,
.europe:focus,
.northamerica:focus,
.oceania:focus,
.africa:focus,
.global:focus {
  background-color: rgb(31, 174, 160) !important;
  color: #fff !important;
}

.asia {
  display: block;
  position: absolute;
  background: #212529 !important;
  bottom: 28%;
  margin-left: 27px;
}

.southamerica {
  display: block;
  position: absolute;
  background: #212529 !important;
  bottom: 35%;
  margin-left: 27px;
}

.europe {
  display: block;
  position: absolute;
  background: #212529 !important;
  bottom: 42%;
  margin-left: 27px;
}

.africa {
  display: block;
  position: absolute;
  background: #212529 !important;
  bottom: 49%;
  margin-left: 27px;
}

.oceania {
  display: block;
  position: absolute;
  background: #212529 !important;
  bottom: 56%;
  margin-left: 27px;
}

.northamerica {
  display: block;
  position: absolute;
  background: #212529 !important;
  bottom: 63%;
  margin-left: 27px;
}

.global {
  display: block;
  position: absolute;
  background: #212529 !important;
  bottom: 70%;
  margin-left: 27px;
}

/* Tabs */

.wrapper {
  margin: 5px auto;
}

.tabs {
  display: flex;
  justify-content: left;
  padding-top: 0.7rem;
}

.tabitem {
  cursor: pointer;
  height: 20px;
  text-align: left;
  padding: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  /* margin-right: 30px; */
}

.tabitem--inactive {
  opacity: 0.5;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.tabitem--inactive:hover {
  opacity: 1;
}

.tabitem__icon {
  color: rgba(255, 255, 255, 0.9);
  font-size: 8px;
}

.tabitem__title {
  color: rgba(255, 255, 255, 0.9);
  font-size: 14px;
}

.content {
  text-align: center;
  margin-top: 5px;
}

